import { Subject } from 'rxjs';
import { environment } from './../../environments/environment';
import { Item } from './../models/item';
import { Product } from './../models/product';
import { Cart } from './../models/cart';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  cart: Cart = new Cart();
  cart$ = new Subject<Cart>();
  tva = environment.tva/100;

  constructor() {
    this.initCart();
  }

  initCart(){
    try {

      let data = JSON.parse(localStorage.getItem('cart'));
      if(data){

        this.cart = data;
        console.log(this.cart);
        this.updateCart();
      }

    } catch (error) {
      console.log(error.message);

    }
  }
  emitCart(){
    this.cart$.next(this.cart);
  }
  addToCart(product: Product){
    const item = this.cart.items.find(item => item.product._id === product._id);

    if(item){
      item.quantity++;
    }else{
      const item = new Item();
      item.product = product;
      item.quantity = 1;
      this.cart.items.push(item);
    }
    this.updateCart();
  }

  updateCart(){
    this.cart.resume = {quantity: 0, costHT: 0, costTaxe: 0, costTTC: 0};
    this.cart.items.forEach(item =>{
      this.cart.resume.quantity += item.quantity;
      this.cart.resume.costHT += item.quantity * item.product.price;
      this.cart.resume.costTaxe += item.quantity * item.product.price * this.tva;
      this.cart.resume.costTTC += item.quantity * item.product.price * (1 + this.tva);
    })
    localStorage.setItem('cart',JSON.stringify(this.cart));
    this.emitCart();
  }

  removeOne(product: Product){
    const item = this.cart.items.find(item => item.product._id === product._id);

    if(item){
      if(item.quantity > 1){
        item.quantity--;
      }else{
        const index = this.cart.items.indexOf(item);
        this.cart.items.splice(index,1);
      }
      this.updateCart();
    }

  }

  removeMany(product: Product){
    const item = this.cart.items.find(item => item.product._id === product._id);
    if(item){
      const index = this.cart.items.indexOf(item);
      this.cart.items.splice(index,1);
      this.updateCart();
    }
  }
}
