import { Data } from './../models/data';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private api = environment.api;
  token: string;
  userId: string;
  isAuth$ = new BehaviorSubject<boolean>(false);

  constructor(private http: HttpClient) {

  }

  checked(userId: string){
    return new Promise((resolve, reject)=>{
      this.http.post(this.api+'/users/check', {userId: userId}).subscribe(
        (data)=>{
          resolve(data);
        },(err)=>{
          reject(err);
        }
      )
    });
  }

  signup(email: string, password: string){
    return new Promise((resolve, reject)=>{
      this.http.post(this.api+'/users/signup', {email: email, password: password}).subscribe(
        (signupData: {status: number, message: string})=>{
          if(signupData.status === 201){
            // authentifier l'utilisateur
            this.signin(email, password)
            .then(()=>{
              resolve(true);
            })
            .catch((err)=>{
              reject(err)
            });
          }else{
            reject(signupData.message);
          }
        },
        (err)=>{
          reject(err)
        }
      )
    })
  }

  signin(email: string, password: string){
    return new Promise((resolve, reject)=>{
      this.http.post(this.api+'/users/login', {email: email, password: password}).subscribe(
        (authData: {token: string, userId: string})=>{
          this.token = authData.token;
          this.userId = authData.userId;
          this.isAuth$.next(true);
          // save authData in local
          if(typeof localStorage !== "undefined"){
            localStorage.setItem('auth',JSON.stringify(authData));
          }
          resolve(true);
        },
        (err)=>{
          reject(err)
        }
      )
    })
  }

  logout(){
    this.isAuth$.next(false);
    this.userId = null;
    this.token = null;
    if(typeof localStorage !== "undefined"){
      localStorage.setItem('auth',null);
    }
  }
}
